<template>
  <div>
    <div class="game">
      <canvas id="canvas" ref="canvas" width="560" height="560"></canvas>
      <p>{{ yiDongCount }}</p>
      <el-button type="primary" @click="NextLevel(-1)"></el-button>
      <el-button type="primary" @click="NextLevel(1)"></el-button>
      <el-button type="primary" @click="NextLevel(0)"></el-button>
      <el-button type="primary" @click="showHelp()"></el-button>
    </div>
    <el-dialog title="" :visible.sync="showhelp" width="30%" :before-close="handleClose">
      <span>{{ msg }}</span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="showhelp = false"> </el-button>
        <el-button type="primary" @click="showhelp = false"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import levels from "../../game/Sokoban/SokobanMapData";
//
function Point(x, y) {
  this.x = x;
  this.y = y;
}
var perPosition = new Point(5, 5); //
export default {
  name: "",
  components: {},
  mixins: [],
  props: {},
  data() {
    return {
      can: null,
      msg: null,
      cxt: null,
      w: 35,
      h: 35,
      curMap: null, //
      curLevel: null, //
      curMan: null, //
      iCurlevel: 0, //
      moveTimes: 0, //
      //
      oImgs: {
        block: require("../../game/Sokoban/images/block.png"),
        wall: require("../../game/Sokoban/images/wall.png"),
        box: require("../../game/Sokoban/images/box.png"),
        ball: require("../../game/Sokoban/images/ball.png"),
        up: require("../../game/Sokoban/images/up.png"),
        down: require("../../game/Sokoban/images/down.png"),
        left: require("../../game/Sokoban/images/left.png"),
        right: require("../../game/Sokoban/images/right.png"),
      },
      block: null,
      wall: null,
      box: null,
      ball: null,
      up: null,
      down: null,
      left: null,
      right: null,
      showhelp: false,
      BumpIntoAwall: 0, //
      yiDongCount: "",
      BumpIntoAwallError: ["!!", "!", "!!!"],
    };
  },
  computed: {},
  watch: {},
  mounted() {
    var _that = this;
    this.can = this.$refs.canvas;
    this.cxt = this.can.getContext("2d");
    this.keyDown();
    _that.imgPreload(_that.oImgs).then((images) => {
      _that.block = images.block;
      _that.wall = images.wall;
      _that.box = images.box;
      _that.ball = images.ball;
      _that.up = images.up;
      _that.down = images.down;
      _that.left = images.left;
      _that.right = images.right;
      _that.init();
    });
  },
  methods: {
    imgPreload(srcs) {
      return new Promise((resolve, reject) => {
        var count = 0;
        var imgNum = 0;
        var images = {};
        for (var key in srcs) {
          imgNum++;
        }
        for (var src in srcs) {
          images[src] = new Image();
          images[src].onload = function () {
            //
            if (++count >= imgNum) {
              resolve(images);
            }
          };
          images[src].src = srcs[src];
        }
      });
    },
    //
    init() {
      //InitMap();
      //DrawMap(levels[0]);
      this.initLevel(); //
      this.showMoveInfo(); //
    },
    //
    InitMap() {
      var _that = this;
      for (var i = 0; i < 16; i++) {
        for (var j = 0; j < 16; j++) {
          _that.cxt.drawImage(_that.block, _that.w * j, _that.h * i, _that.w, _that.h);
        }
      }
    },
    //
    DrawMap(level) {
      var _that = this;
      for (var i = 0; i < level.length; i++) {
        for (var j = 0; j < level[i].length; j++) {
          var pic = _that.block; //
          switch (level[i][j]) {
            case 1: //
              pic = _that.wall;
              break;
            case 2: //
              pic = _that.ball;
              break;
            case 3: //
              pic = _that.box;
              break;
            case 4: //
              pic = _that.curMan; // 
              //
              perPosition.x = i;
              perPosition.y = j;
              break;
            case 5: //
              pic = _that.box;
              break;
          }
          // 
          _that.cxt.drawImage(pic, _that.w * j - (pic.width - _that.w) / 2, _that.h * i - (pic.height - _that.h), pic.width, pic.height);
        }
      }
    },

    //
    initLevel() {
      var _that = this;
      _that.curMap = _that.copyArray(levels[_that.iCurlevel]); //
      _that.curLevel = levels[_that.iCurlevel]; //
      _that.curMan = _that.down; //
      _that.InitMap(); //
      _that.DrawMap(_that.curMap); //
    },
    //
    NextLevel(i) {
      var _that = this;
      //iCurlevel
      _that.iCurlevel = _that.iCurlevel + i;
      if (_that.iCurlevel < 0) {
        _that.iCurlevel = 0;
        return;
      }
      var len = levels.length;
      if (_that.iCurlevel > len - 1) {
        _that.iCurlevel = len - 1;
      }
      _that.initLevel(); //
      _that.moveTimes = 0; //
      _that.showMoveInfo(); //
    },
    //
    go(dir) {
      var _that = this;
      var p1, p2;
      switch (dir) {
        case "up":
          _that.curMan = _that.up;
          //
          p1 = new Point(perPosition.x - 1, perPosition.y);
          p2 = new Point(perPosition.x - 2, perPosition.y);
          break;
        case "down":
          _that.curMan = _that.down;
          p1 = new Point(perPosition.x + 1, perPosition.y);
          p2 = new Point(perPosition.x + 2, perPosition.y);
          break;
        case "left":
          _that.curMan = _that.left;
          p1 = new Point(perPosition.x, perPosition.y - 1);
          p2 = new Point(perPosition.x, perPosition.y - 2);
          break;
        case "right":
          _that.curMan = _that.right;
          p1 = new Point(perPosition.x, perPosition.y + 1);
          p2 = new Point(perPosition.x, perPosition.y + 2);
          break;
      }
      //
      if (_that.Trygo(p1, p2)) {
        _that.moveTimes++;
        _that.showMoveInfo();
      }
      //
      _that.InitMap();
      //
      _that.DrawMap(_that.curMap);
      //
      if (_that.checkFinish()) {
        this.$confirm("!", "", {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning",
        })
          .then(() => {
            _that.NextLevel(1);
          })
          .catch(() => {
            _that.NextLevel(0);
          });
      }
    },

    //
    checkFinish() {
      var _that = this;
      for (var i = 0; i < _that.curMap.length; i++) {
        for (var j = 0; j < _that.curMap[i].length; j++) {
          //
          if ((_that.curLevel[i][j] == 2 && _that.curMap[i][j] != 3) || (_that.curLevel[i][j] == 5 && _that.curMap[i][j] != 3)) {
            return false;
          }
        }
      }
      return true;
    },
    //
    Trygo(p1, p2) {
      var _that = this;
      //
      if (p1.x < 0) {
        this.$message.error("!!!!");
        return false;
      }
      //
      if (p1.y < 0) {
        this.$message.error("!!!!");
        return false;
      }
      //
      if (p1.x > _that.curMap.length) {
        this.$message.error("!!!!");
        return false;
      }
      //
      if (p1.y > _that.curMap[0].length) {
        this.$message.error("!!!!");
        return false;
      }
      //
      if (_that.curMap[p1.x][p1.y] == 1) {
        _that.BumpIntoAwall++;
        if (_that.BumpIntoAwall <= _that.BumpIntoAwallError.length) {
          this.$message.error(_that.BumpIntoAwallError[_that.BumpIntoAwall - 1]);
        } else {
          this.$message.error("!");
        }
        return false;
      }
      if (_that.curMap[p1.x][p1.y] == 3 || _that.curMap[p1.x][p1.y] == 5) {
        //(/)
        if (_that.curMap[p2.x][p2.y] == 1 || _that.curMap[p2.x][p2.y] == 3) {
          return false;
        }
        //
        _that.curMap[p2.x][p2.y] = 3; //
        //console.log(curMap[p2.x][p2.y]);
      }
      //
      _that.curMap[p1.x][p1.y] = 4; //
      //
      var v = _that.curLevel[perPosition.x][perPosition.y];
      if (v != 2) {
        //
        if (v == 5) {
          //5 
          v = 2; //
        } else {
          v = 0; //
        }
      }
      //
      _that.curMap[perPosition.x][perPosition.y] = v;
      //
      perPosition = p1;
      _that.BumpIntoAwall = 0;
      // true 
      return true;
    },

    //
    showMoveInfo() {
      this.yiDongCount = "" + (this.iCurlevel + 1) + " : " + this.moveTimes;
    },
    //
    showHelp() {
      this.showhelp = !this.showhelp;
      if (this.showhelp) {
        this.msg = "";
      } else {
        this.showMoveInfo();
      }
    },

    //
    copyArray(arr) {
      var b = []; //
      for (var i = 0; i < arr.length; i++) {
        b[i] = arr[i].concat(); //
      }
      return b;
    },
    handleClose() {
      this.showhelp = false;
    },
    keyDown() {
      // 
      var _that = this;
      document.onkeydown = (e) => {
        //
        let e1 = e || window.event || arguments.callee.caller.arguments[0]; //:-37;-38-39;-40 //
        switch (e1.key) {
          case "ArrowLeft": //
            _that.go("left");
            break;
          case "ArrowUp": //
            _that.go("up");
            break;
          case "ArrowRight": //
            _that.go("right");
            break;
          case "ArrowDown": //
            _that.go("down");
            break;
        }
      };
    },
  },
};
</script>
<style lang="scss" scoped>
body {
  overflow: hidden;
}
.game {
  width: 560px;
  margin: 50px auto;
}
</style>
