<template>
  <div>
    <p>
      
    </p>
    <br />
    <input type="file" name="" id="photo" @change="fileAdd" />
    <br />
    <p class="display-ib">
      <span>,{{ defaultImgSize }} M</span>
      <img :src="defaultImgSrc" alt="" />
    </p>
    <p class="display-ib">
      <span>,{{ compressImgSize }} M</span>
      <img :src="compressImgSrc" alt="" />
    </p>
  </div>
</template>
<script>
export default {
  name: "",
  components: {},
  mixins: [],
  props: {},
  data() {
    return {
      defaultImgSrc: null,
      defaultImgSize: null,
      compressImgSrc: null,
      compressImgSize: null,
    };
  },
  computed: {},
  watch: {},
  mounted() {},
  methods: {
    imgUpload2(file) {
      let formdata = new FormData();
      formdata.append("file", file);
      console.log(file);
      axios.post("http://127.0.0.1:3001/uploadimg", formdata).then((res) => {
        console.log(res);
        if (res.data.code == 200) {
          photo.value = res.data.path;
        } else {
          alert(res.data.msg);
        }
      });
    },
    fileAdd(e) {
      console.log(e)
      let file = e.target.files[0];
      console.log(file)
      //
      if (file.type.indexOf("image") == -1) {
        this.$message.error("")
        return
      }else if(!/\.(jpg|jpeg|png|JPG|PNG)$/.test(file.name)){
        this.$message.error("jpgpng")
        return
      } else {
        let reader = new FileReader();
        let image = new Image();
        let _this = this;
        reader.readAsDataURL(file);
        reader.onload = function () {
          file.src = this.result;
          image.onload = function () {
            let width = image.width;
            let height = image.height;
            file.width = width;
            file.height = height;
            _this.imgL = file.src; //
          };
          console.log(file);
          image.src = file.src; //
          _this.defaultImgSrc = file.src;
          _this.defaultImgSize = parseFloat(file.size / 1024 /1204).toFixed(2);
          if (file.size / 1024 > 1025) {
            console.log(file.width);
            //1M 
            _this.imgCompress(file, { quality: 0.7, img_scale: 0.5 });
          } else {
            // _this.imgUpload2(file)
          }
        };
      }
    },

    //
    imgCompress(path, obj) {
      //pathobj
      let _this = this; //this vue_this
      var img = new Image();
      img.src = path.src;
      img.onload = function () {
        var that = this; //this imgthat
        // 
        var w = that.width,
          h = that.height,
          scale = w / h;
        // w = obj.width || w;
        // h = obj.height || (w / scale);
        w = obj.img_scale ? w * obj.img_scale : w;
        h = obj.img_scale ? h * obj.img_scale : h;
        console.log(w, h, obj);
        var quality = 0.7; // 0.7
        //canvas
        var canvas = document.createElement("canvas");
        var ctx = canvas.getContext("2d");
        // 
        var anw = document.createAttribute("width");
        anw.nodeValue = w;
        var anh = document.createAttribute("height");
        anh.nodeValue = h;
        canvas.setAttributeNode(anw);
        canvas.setAttributeNode(anh);
        ctx.drawImage(that, 0, 0, w, h);
        // 
        if (obj.quality && obj.quality <= 1 && obj.quality > 0) {
          quality = obj.quality;
        }
        // quality
        var base64 = canvas.toDataURL("image/jpeg", quality);
        // base64
        var urlFile = _this.convertBase64UrlToBlob(base64); //base64size
        _this.compressImgSrc = base64;
        _this.compressImgSize = parseFloat(urlFile.size / 1024 /1024).toFixed(2);
        console.log(urlFile);
        // _this.imgUpload2(urlFile)
      };
    },

    convertBase64UrlToBlob(urlData) {
      //base64fileBlob
      //base64{size: "", type: ""}
      var arr = urlData.split(","),
        mime = arr[0].match(/:(.*?);/)[1],
        bstr = atob(arr[1]),
        n = bstr.length,
        u8arr = new Uint8Array(n);
      while (n--) {
        u8arr[n] = bstr.charCodeAt(n);
      }
      return new Blob([u8arr], { type: mime });
    },
  },
};
</script>
<style lang="scss" scoped>
.display-ib {
  display: inline-block;
}
img{
  width: 500px;
}
</style>
